import { useEffect, useRef} from 'react'

import SafetyBar from './safetyBar';
import NaughtyBar from './naughtyBar';
import RewardBar from './rewardBar';
import BugGuidance from './bugGuidance'

import virusyellow1 from './../images/virusyellow-1-250-min.png';
import virusorange1 from './../images/virusorange-1-250-min.png';
import virusred1 from './../images/virusred-1-250-min.png';
import virusyellow2 from './../images/virusyellow-2-250-min.png';
import virusorange2 from './../images/virusorange-2-250-min.png';
import virusred2 from './../images/virusred-2-250-min.png';

import virusunknown from './../images/virusunknown-250-min.png';


const GameScreen = (props) => {


  const {laser1,phit,music,stop,isPlaying,sound,messagePool,currentRound, currentTreatment, setCurrentTreatment, maxExtraTime, bugSettings, rewardCount, setRewardCount, extraTime, setExtraTime, squashed,setSquashed, soundOn, bugs, setBugs, timer,score,setScore} = props
  const progressMaxTime = 1000

  const bugimages = {
    yellow1: virusyellow1,
    red1: virusred1,
    orange1: virusorange1,
    yellow2: virusyellow2,
    red2: virusred2,
    orange2: virusorange2,
    grey: virusunknown
  }

  
  const timeToRise = 500
  const timeToFall = 1000
  const timeToFallSquashed = 250

  const handleMouseUp = (e) => {
    e.preventDefault()
    let newBugs = [...bugs]
    let b = newBugs.filter(b => b.id === parseInt(e.target.id))[0]
    if (b.progress >  b.progressSafeTarget) {
      let newSquashed = [...squashed]
      newSquashed.push([b.id, Date.now()])
      setSquashed(newSquashed)

      if (soundOn) laser1()
      setScore(score + 1)
      b.cured = true
      let newExtraTime = (b.progress > 0.5 ? 0.5 : b.progress - b.progressTarget) * progressMaxTime * (1-b.progressTarget) 
      if (rewardCount + 1 >= 5) {
        setRewardCount(0)
        setExtraTime(extraTime - maxExtraTime/2 + newExtraTime < 0 ? 0 : extraTime - maxExtraTime/2 + newExtraTime )
      } else {
        setRewardCount(rewardCount + 1)
        setExtraTime(extraTime + newExtraTime)

      }
    }
    b.treated = false
    setBugs(newBugs)
  }

  const handleMouseLeave = (e) => {
    e.preventDefault()
    let newBugs = [...bugs]
    let b = newBugs.filter(b => b.id === parseInt(e.target.id))[0]
    b.treated = false
    setBugs(newBugs)
  }
  const handleMouseDown = (e) => {
    e.preventDefault()
    let newBugs = [...bugs]
    let b = newBugs.filter(b => b.id === parseInt(e.target.id))[0]

    if (b && bugSettings[b.color].treatable === 0 && !squashed.filter(s=> s[0] === parseInt(e.target.id))[0]) {
      
      let newSquashed = [...squashed]
      newSquashed.push([parseInt(e.target.id), Date.now()])
      setRewardCount(rewardCount - 1)
      setSquashed(newSquashed)
      if (soundOn) phit()
      //setScore(score - 1)
      setExtraTime(extraTime + maxExtraTime/10)
    } else if (b && (bugSettings[b.color].treatable === currentTreatment || bugSettings[b.color].treatable === 3)) {
    

      if (!b.treated) b.timeTreated = Date.now()

      b.treated = true
    }
    setBugs(newBugs)
  }

  const animateEvery = 1000/60

  const requestRef = useRef()
  const lastCheckRef = useRef(Date.now()-500)

  const overlapCheck = (newBugPosition) => {
    return bugs.every(b => 
      (Math.abs(b.x - newBugPosition[0]) > 15  && Math.abs(b.y - newBugPosition[1]) > 15)
    )
  }

  
  

  const bugCheck = () => {

    let newbugs = [...bugs]
    
    if (   Date.now() - lastCheckRef.current > 500 ) {
      
      let newBugPosition =  [Math.ceil(Math.random() * 75) + 12.5, Math.ceil(Math.random() * 75)+12.5]
      if (overlapCheck(newBugPosition)) {
        
        if (newbugs.length === 0 || (newbugs.length < 4 && Math.random() > 0.5)) {
          let safetyLength = 0.05
          let target = 0.125 + 0.25 * Math.random()
          let colorRand = Math.random()
          let color = "yellow"
          if (colorRand > bugSettings.yellow.appearanceProb && colorRand <= bugSettings.yellow.appearanceProb + bugSettings.red.appearanceProb ){
            color = "red" 
          } else
          if (colorRand > bugSettings.yellow.appearanceProb + bugSettings.red.appearanceProb && colorRand <= bugSettings.yellow.appearanceProb + bugSettings.red.appearanceProb + bugSettings.orange.appearanceProb ){
            color = "orange" 
          }else
          if (colorRand > bugSettings.yellow.appearanceProb + bugSettings.red.appearanceProb + bugSettings.orange.appearanceProb  ){
            color = "grey" 
          }
          let newBug = {
            id: Date.now(),
            timeAppeared: Date.now(),
            timeToStayUp: 2500 + Math.floor(Math.random() * 500), 
            color: color,
            state: 0,
            x: newBugPosition[0],
            y: newBugPosition[1],
            progress: 0,
            progressSafeTarget: target - safetyLength,
            progressTarget: target,
            treated: false,
            timeTreated: false
          }
          newBug.variant = newBug.color === "grey" ? "" : Math.ceil(Math.random() * 2)
          newbugs.push(newBug)
          lastCheckRef.current = Date.now()

        }

      }
      
      


    } else {
      //console.log("no bug")
      //console.log(lastCheckRef.current)
    }



    newbugs = newbugs.map(b => {
      if (squashed.filter(s => s[0] === b.id)[0] && !b.timeSquashed) {
        b.timeSquashed = squashed.filter(s => s[0] === b.id)[0][1]

        b.state = 3
      } else
      if (b.state === 0 && Date.now() - b.timeAppeared > timeToRise) {
        b.state = 1
      } else
      if (b.state === 1 && Date.now() - b.timeAppeared > timeToRise + b.timeToStayUp) {
        b.state = 2
      } else
      if (b.state === 2 && Date.now() - b.timeAppeared  > timeToRise + b.timeToStayUp + timeToFall) {
        b.state = 4
      } else
      if (b.state === 3 && Date.now() - b.timeSquashed  >  timeToFallSquashed) {
        b.state = 4
      } 
      
      if (b.progress >= 1 && b.progress < 999) {
          b.progress = 999
      } else
      if (b.treated && b.progress < 999) {
        b.progress = b.progress + (Date.now() - b.timeTreated)/progressMaxTime
        b.timeTreated = Date.now()
      }
      
      return b
    })
    let missedCount = newbugs.filter(b => b.state === 4 && bugSettings[b.color].treatable > 0 && !b.cured).length
    if (missedCount && soundOn) phit()

    setExtraTime(extraTime + missedCount * 200)
    setRewardCount(rewardCount - missedCount)

    newbugs = newbugs.filter(b => b.state < 4)
    setBugs(newbugs)
  }

  useEffect(() => { 
    if (soundOn && !isPlaying) {
      music()
    } else if (!soundOn) {
      stop()
    }
    return () => stop()
  },[music,isPlaying,stop,soundOn,sound])


  useEffect(() => {
    

    const animationCheck = () => {
      bugCheck()      
    }

    requestRef.current = setInterval(animationCheck,animateEvery)

    return () => clearInterval(requestRef.current)
  }
  // eslint-disable-next-line
  ,[bugs])

  const calcHeight = (b) => {
    if (b.state === 0) {
      return 100 * (Date.now() - b.timeAppeared) / timeToRise + "%"
    } else
    if (b.state === 1) {
      return "100%"
    } else
    if (b.state === 2) {
      return 100 - (100 * (Date.now() - (b.timeAppeared + timeToRise + b.timeToStayUp)) / timeToFall) + "%"
    } else
    if (b.state === 3) {
      let startState = 1
      if (b.timeSquashed - b.timeAppeared < timeToRise) {
        startState = (b.timeSquashed - b.timeAppeared)/timeToRise
      } else
      if (b.timeSquashed - b.timeAppeared > timeToRise + b.timeToStayUp){
        startState = (b.timeSquashed - b.timeAppeared - (timeToRise + b.timeToStayUp))/timeToFallSquashed
      }
      console.log(startState)
      return 100 * (startState - ((Date.now() - b.timeSquashed )/ timeToFallSquashed)) + "%"
    } else
    if (b.state === 4) {
      return "0%"
    }
  }

  useEffect(() => {

    const handleUserKeyPress = (e) => {

      if (e.keyCode === 32) {
        setCurrentTreatment(currentTreatment === 1 ? 2 : 1)
      }
    }


  
    
    window.addEventListener('keydown', handleUserKeyPress);

  
    return () => {
      window.removeEventListener('keydown', handleUserKeyPress)

    }
  },[currentTreatment,setCurrentTreatment])

  


 

  return (
    <>
          <h1>BASH THE BACTERIA</h1>
      <SafetyBar bugs={bugs} />
      <div id="scorebar">
        <div id="timer">
        {Math.floor(timer)}
        </div>
        
        </div>

        <div id="bugboxcontainer">
        <RewardBar rewardCount={rewardCount} />

        <div id="bugbox">

          {bugs.map(b => {
            return <div
            id={b.id}
            key={b.id}
          style={{
            position: "absolute",
            bottom: 100 - b.y + "%",
            width: 12.5 + "%",
            height: 12.5 + "%",
            left:  b.x + "%",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",

            justifyContent: "end"

          }} 
          disabled={b.state > 2}
          onTouchCancel={handleMouseLeave}
          onTouchEnd={handleMouseUp}
          onTouchStart={handleMouseDown}
          onMouseDown={handleMouseDown}
          onMouseUp={handleMouseUp}
          onMouseLeave={handleMouseLeave}
          onContextMenu={handleMouseLeave}
          ><div 
          id={b.id}

          style={{
            width: 100 * (b.progress < 999 ? 1-b.progressTarget + b.progressTarget  * (1-b.progress) : b.progressTarget ) + "%",
            height: 100 * (b.progress < 999 ? 1-b.progressTarget  + b.progressTarget  * (1-b.progress) : b.progressTarget ) + "%",

            display: "flex",
            flexDirection: "column",
            justifyContent: "end"
          }}
          >
            <div
          id={b.id}

          style={{
            width: "100%",
            overflow: "hidden",
            height: calcHeight(b),
            backgroundImage: `url(${bugimages[b.color + b.variant]})`,
            backgroundPosition: 'top',
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat'
          }}  
          ></div>
          </div>
          </div>

          })}
        </div>
        <NaughtyBar maxExtraTime={maxExtraTime} extraTime={extraTime} />
        
        <BugGuidance messagePool={messagePool} bugimages={bugimages} currentRound={currentRound} currentTreatment={currentTreatment} setCurrentTreatment={setCurrentTreatment}/>
        
        </div>
      </>
        

  )
}

export default GameScreen