import './App.css';
import { useEffect, useState } from 'react'
import StartScreen from './components/startScreen.jsx'
import RoundScreen from './components/roundScreen.jsx'
import EndScreen from './components/endScreen.jsx'
import GameScreen from './components/gameScreen.jsx'
import { VolumeOff, VolumeUp } from '@material-ui/icons';
import ReactGA from 'react-ga';
import useSound from 'use-sound';

import laser1Sfx from './sounds/laser1.mp3';
import phitSfx from './sounds/phit.mp3';
import musicSfx from './sounds/music.mp3';





const parse = require('csv-parse')


const App = () => {

  ReactGA.initialize('UA-214460310-1');
  ReactGA.pageview(window.location.pathname + window.location.search);

  const [laser1] = useSound(laser1Sfx);
  const [phit] = useSound(phitSfx);
  const [music, { stop, isPlaying, sound }] = useSound(musicSfx)

  const [score, setScore] = useState(0);
  const [extraTime, setExtraTime] = useState(0);
  const [rewardCount,setRewardCount] = useState(0)
  const [currentRound,setCurrentRound] = useState(0)
  const [roundScores,setRoundScores] = useState([
    {complete: 0, time: 0, bugs: 0},
    {complete: 0, time: 0, bugs: 0},
    {complete: 0, time: 0, bugs: 0},
    {complete: 0, time: 0, bugs: 0}
  ])
  const [soundOn,setSoundOn] = useState(true)
  const [bugs, setBugs] = useState([]);
  const [currentTreatment, setCurrentTreatment] = useState(1);

  const [gameState, setGameState] = useState(0)
  const [timerStart,setTimerStart] = useState(0)
  const [timer,setTimer] = useState(0)
  const [messagePool,setMessagePool] = useState(0)
  const [squashed,setSquashed] = useState([])

  const roundLength = 60
  const maxExtraTime = 3000
  const startGame = () => {
    setCurrentRound(1)
    setGameState(1)
  }

  const [bugSettings,setBugSettings] = useState({
    yellow: { appearanceProb: 0.9, treatable: 1 },
    red: { appearanceProb: 0.1, treatable:  0 },
    orange: { appearanceProb: 0, treatable: -1 },
    grey: {appearanceProb: 0, treatable: -1},
  })

  const nextRound = () => {
    setBugs([])
    setGameState(2)
    setScore(0)
    setExtraTime(0)
    setTimerStart(Date.now())
    setTimer(0)
    setCurrentTreatment(1)
    setRewardCount(0)
    if (currentRound === 1) {
      setBugSettings({
        yellow: { appearanceProb: 0.9, treatable: 1 },
        red: { appearanceProb: 0.1, treatable:  0 },
        orange: { appearanceProb: 0, treatable: -1 },
        grey: {appearanceProb: 0, treatable: -1},
      })
    } else
    if (currentRound === 2) {
      setBugSettings({
        yellow: { appearanceProb: 0.5, treatable: 1 },
        red: { appearanceProb: 0.5, treatable:  0 },
        orange: { appearanceProb: 0, treatable: -1 },
        grey: {appearanceProb: 0, treatable: -1},
      })
    } else
    if (currentRound === 3) {
      setBugSettings({
        yellow: { appearanceProb: 0.45, treatable: 1 },
        red: { appearanceProb: 0.45, treatable:  2 },
        orange: { appearanceProb: 0.1, treatable: 0 },
        grey: {appearanceProb: 0, treatable: -1},
      })
    } else
    if (currentRound === 4) {
      setBugSettings({
        yellow: { appearanceProb: 0.3, treatable: 1 },
        red: { appearanceProb: 0.3, treatable:  2 },
        orange: { appearanceProb: 0.3, treatable: 3 },
        grey: {appearanceProb: 0.1, treatable: 0},
      })
    }
  }

  const restartGame = () => {
    setGameState(0)
    setScore(0)
    setSquashed([])
    setBugs([])
  }

  const displayGameState = (gameState) => {
    if (gameState === 0) {
      return <StartScreen messagePool={messagePool} startGame={startGame} />
    } else
    if (gameState === 1) {
      return <RoundScreen roundScores={roundScores} messagePool={messagePool} currentRound={currentRound} nextRound={nextRound} />
    } else
    if (gameState === 2) {
      return <GameScreen laser1={laser1} phit={phit} music={music} stop={stop} isPlaying={isPlaying} sound={sound} messagePool={messagePool} currentRound={currentRound} currentTreatment={currentTreatment} setCurrentTreatment={setCurrentTreatment}  maxExtraTime={maxExtraTime} bugSettings={bugSettings} rewardCount={rewardCount} setRewardCount={setRewardCount} extraTime={extraTime} setExtraTime={setExtraTime} squashed={squashed} setSquashed={setSquashed} bugs={bugs} setBugs={setBugs} soundOn={soundOn}  timer={timer} score={score} setScore={setScore}  />
    } else {
      return <EndScreen roundScores={roundScores} messagePool={messagePool} restartGame={restartGame} />
    }
  }

  useEffect(() => {
    let timeout
    if (gameState === 2 && extraTime >= maxExtraTime && currentRound === 4) {
      setGameState(3)
    } else 
    if (gameState === 2 && extraTime >= maxExtraTime) {
      setGameState(1)
      setRoundScores(roundScores => {
        let newScores = [...roundScores]
        newScores[currentRound - 1].bugs = score
        newScores[currentRound - 1].complete = 1
        newScores[currentRound - 1].time = timer
        return newScores
      })
      setCurrentRound(currentRound + 1)
    } else
    if (gameState === 2 && timer > roundLength && currentRound === 4) {
      setGameState(3)
      setRoundScores(roundScores => {
        let newScores = [...roundScores]
        newScores[currentRound - 1].bugs = score
        newScores[currentRound - 1].complete = 1
        newScores[currentRound- 1].time = roundLength
        return newScores
      })
    } else 
    if (gameState === 2 && timer > roundLength) {
      setGameState(1)
      setRoundScores(roundScores => {
        let newScores = [...roundScores]
        newScores[currentRound - 1].bugs = score
        newScores[currentRound - 1].complete = 1
        newScores[currentRound- 1].time = roundLength
        return newScores
      })
      setCurrentRound(currentRound + 1)
    } else 
    if (gameState === 2) {
      timeout = setTimeout(() => {
        setTimer((Date.now()-timerStart)/1000)
      }, 100);
    }
    
    return () => clearTimeout(timeout);

  },[timerStart,timer,setTimer,gameState,extraTime,currentRound,score])


  useEffect(() => {
    if (!messagePool) {

      const loadMessageData = (parser,data) => {
        data.split("\n").forEach(d => 
          parser.write(d)
        )
      }
        
      let messages = {}
      const parser = parse({
        delimiter: ','
      })

      parser.on('readable', function(){
        let record
        // eslint-disable-next-line
        while (record = parser.read()) {

          let type = record[0]
          if (!messages[`${type}Messages`]){
            messages[`${type}Messages`] = []
          }
          messages[`${type}Messages`].push([record[1],record[2],record[3]])
        }
      })
  
      parser.on('error', function(err){
        console.error(err.message)
      })

      const csvurl = "https://docs.google.com/spreadsheets/d/e/2PACX-1vQ9xqpcP13MczJVdD1VVa_CMtHrOM8IeV_Gtt3PebY3kbiueG4R1l6y0H1-ydpoRFL_hhxJay0Wmvbi/pub?gid=0&single=true&output=csv"
      fetch(csvurl).then((response) => {
        return response.text();
      })
      .then((csvdata) => {
        
        loadMessageData(parser,csvdata)
        parser.end()
        setMessagePool(messages)

        
      })
    }
  },[messagePool,setMessagePool])
  const soundToggle = () => {
    setSoundOn(soundOn ? false : true)
  }

  return (
    <div className="App">
      
      <div className="container">
      
        {messagePool ? displayGameState(gameState) : ""}
          <div id="volumecontrol" onClick={soundToggle}>
        {
          soundOn ?  <VolumeUp /> : <VolumeOff />
        }

      </div>
      </div>
    </div>
  );
}

export default App;