import ReactMarkdown from 'react-markdown'
import logo from './../images/logo.svg'

const EndScreen = (props) => {

  const {roundScores, messagePool, restartGame} = props

  const bugScore = () => {
    return roundScores.map (r => r.complete).reduce((a, b) => a + b, 0)
  }

  const timeLasted = () => {
    return Math.floor(roundScores.map (r => r.time).reduce((a, b) => a + b, 0))
  }

  return (
   
  messagePool ? <>
         <h1>BASH THE BACTERIA</h1>

   <p id="initialEndScore">You completed {roundScores.map (r => r.complete).reduce((a, b) => a + b, 0)} out of 4 rounds and lasted {timeLasted()} seconds!</p>
   <div id="scores">
      <div className="scoreLine">
     <div className="scoreName">Time score:</div>
     <div className="scoreText">{timeLasted()} seconds * 10 = {timeLasted() * 10} points!</div>
  </div>
  <div className="scoreLine">
     <div className="scoreName">Bacteria score:</div>
     <div className="scoreText">{bugScore()} treated * 10 = {bugScore() * 10} points!</div>
    </div>

    <div className="scoreLine scoreLineF">
     <div className="scoreName">Final score:</div>
     <div className="scoreText">{timeLasted() * 10 + bugScore() * 10} points!</div>
    </div>
    </div>

  
   

   {String(messagePool.outroMessages[0][0]).split("<<").map ( (p, i) =>
    

    p.trim().length > 0 ? 
       <ReactMarkdown key={i}>{p}</ReactMarkdown> : "")}
   
   <div onClick={restartGame}  className="startButton endButton" ><span>RESTART GAME</span></div>
   <div id="logo" style={{
   marginTop: "0.5em",
   width: "100%",
   zIndex: -1,
    backgroundImage: `url(${logo})`,
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center"
  }}></div>
   
   </>
   
   
   : ""
   
  )
}

export default EndScreen