const RewardBar = (props) => {

  const {rewardCount} = props
  const rewardWidth = () => {
    if (rewardCount > 5) {
      return 1
    } else {
      return rewardCount/5
    }
  }
return (
  <div id="rewardbar"
        
  style={{
    display: "flex",
    backgroundColor:  "white",
    justifyContent: "end",
    boxSizing: "border-box",
    border:"1px black solid"
  }}

  ><div id="rewardprogressbar" style={{
      position: "absolute",
      width: "100%",
      backgroundColor: "green",
      height: 100 * rewardWidth() + "%",
      bottom: 0
  }}
  >
  </div></div>
)
      }

export default RewardBar