const NaughtyBar = (props) => {

  const {extraTime,maxExtraTime} = props
  const naughtyWidth = () => {
    if (extraTime > maxExtraTime) {
      return maxExtraTime
    } else {
      return extraTime/maxExtraTime
    }
  }
return (
  <div id="naughtybar"
        
  style={{
    display: "flex",
    backgroundColor:  "white",
    justifyContent: "end",
    boxSizing: "border-box",
    border:"1px black solid"
  }}

  ><div id="naughtyprogressbar" style={{
      position: "absolute",
      width: "100%",
      backgroundColor: "red",
      height: 100 * naughtyWidth() + "%",
      bottom: 0
  }}
  >
  </div></div>
)
      }

export default NaughtyBar