import ReactMarkdown from 'react-markdown'
import logo from './../images/logo.svg'

const RoundScreen = (props) => {

  const {roundScores, messagePool, nextRound, currentRound } = props

  const bugScore = () => {
    return roundScores.map (r => r.bugs).reduce((a, b) => a + b, 0)
  }

  const timeLasted = () => {
    return Math.floor(roundScores.map (r => r.time).reduce((a, b) => a + b, 0))
  }

  const successFail = () => {
    if (currentRound === 1 || roundScores[currentRound-2].complete ) {
      return "Success"
    } else {
      return "Fail"
    }
  }
  return (
   
  messagePool ? <>
         <h1>BASH THE BACTERIA</h1>

   <p id="initialEndScore">Round {currentRound}</p>
   {String(messagePool[`round${currentRound}${successFail()}Messages`][0][0]).split("<<").map ( (p, i) =>
    

    p.trim().length > 0 ? 
       <ReactMarkdown key={i}>{p}</ReactMarkdown> : "")}
       {currentRound > 1 ? 
       <div id="scores">
      <div className="scoreLine">
     <div className="scoreName">Time score:</div>
     <div className="scoreText">{timeLasted()} seconds * 10 = {timeLasted() * 10} points!</div>
  </div>
  <div className="scoreLine">
     <div className="scoreName">Bacteria score:</div>
     <div className="scoreText">{bugScore()} treated * 10 = {bugScore() * 10} points!</div>
    </div>

    <div className="scoreLine">
     <div className="scoreName">Total score:</div>
     <div className="scoreText">{timeLasted() * 10 + bugScore() * 10} points!</div>
    </div>
    </div>

      : "" }
   <div onClick={nextRound}  className="startButton endButton" ><span>START ROUND</span></div>
   <div id="logo" style={{
   marginTop: "0.5em",
   width: "100%",
   zIndex: -1,
    backgroundImage: `url(${logo})`,
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center"
  }}></div>
   
   </>
   
   
   : ""
   
  )
}

export default RoundScreen